import './App.css';
import {useState} from "react";
import {
  Routes,
  Route,
} from "react-router-dom";
import Palette from "./components/Palette";
import MonaLisa from "./components/books/MonaLisa";
import Flower from "./components/books/Flower";
import Tracery from "./components/books/Tracery";
import Train from "./components/books/Train";
import Nav from "./components/Nav";

const defaultColor = '#166035';

const colors = [
  '#9FFF28', '#45C063', '#166035',
  '#FEFF24', '#F6BA0F', '#FC8A34',
  '#E86B6C', '#FC3646', '#B4042D',
  '#F600B0', '#9D00ED', '#390070',
  '#69F8FF', '#0E00FD', '#0E007C',
  '#C4965B', '#845F44', '#381304',
  '#ffffff', '#aaaaaa', '#000000',
];

function App() {
  const [chosenColor, setChosenColor] = useState(defaultColor);
  const [isPaletteBtnActive, setPaletteBtn] = useState(false);
  const {width} = window.screen;
  const fillColor = e => {
    e.target.style.fill = chosenColor;
  }
  const bookStyle = {display: isPaletteBtnActive && width < 567 ? 'none' : 'block'};
  const paletteStyle = {display: isPaletteBtnActive || width > 567 ? 'flex' : 'none'};

  return (
    <div className="App">
      <Nav/>
      <div
        className="palette-btn"
        style={{background: chosenColor}}
        onClick={() => {
          if (isPaletteBtnActive) {
            setPaletteBtn(false);
          } else {
            setPaletteBtn(true);
          }
        }}/>
      <div className="container">
        <Routes>
          <Route path="/"/>
          <Route path="/mona-lisa/" element={
            <div className="book">
              <div className="book-item" style={bookStyle}>
                <MonaLisa onFill={fillColor}/>
              </div>
              <Palette
                style={paletteStyle}
                colors={colors}
                currentColor={chosenColor}
                onChangeColor={color => setChosenColor(color)}
              />
            </div>
          }/>
          <Route path="/flower/" element={
            <div className="book">
              <Flower onFill={fillColor}/>
              <Palette
                colors={colors}
                currentColor={chosenColor}
                onChangeColor={color => setChosenColor(color)}
              />
            </div>
          }/>
          <Route path="/tracery/" element={
            <div className="book">
              <div className="book-item" style={bookStyle}>
                <Tracery onFill={fillColor}/>
              </div>
              <Palette
                style={paletteStyle}
                colors={colors}
                currentColor={chosenColor}
                onChangeColor={color => setChosenColor(color)}
              />
            </div>
          }/>
          <Route path="/train/" element={
            <div className="book">
              <div className="book-item" style={bookStyle}>
                <Train onFill={fillColor}/>
              </div>
              <Palette
                style={paletteStyle}
                colors={colors}
                currentColor={chosenColor}
                onChangeColor={color => setChosenColor(color)}
              />
            </div>
          }/>
        </Routes>
      </div>
    </div>
  );
}

export default App;
