import React from "react";
import { Link } from "react-router-dom";
import "./Nav.css";
import {translations} from "../utils/LocalizedStrings";

export default class Nav extends React.Component {
  render() {
    const {width} = window.screen;

    return (
      <div id="nav">
        <h1 className="nav-header">{translations.coloring_book}</h1>
        <nav>
          <Link to="/mona-lisa">{translations.mona_lisa}</Link>
          <Link to="/train">{translations.train}</Link>
          <Link to="/tracery">{translations.tracery}</Link>
          {width > 1365 ? <Link to="/flower">{translations.flower}</Link> : ''}
        </nav>
      </div>
    );
  }
}
